import fetch from "isomorphic-unfetch";

import MasterPage from "../componentes/Master";
import Precio from "../componentes/Precio";

const Index = (props) => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del BitCoin</h2>
                <Precio
                    precio={props.precioBitcoin}
                />
            </div>

            <div className="col-md-8">
                <h2>Noticias sobre BitCoin</h2>
                {console.log(props.noticiasBitcoin)}
            </div>
            <div className="col-md-4">
                <h2>Pr√≥ximos Eventos BitCoin</h2>
            </div>
        </div>
    </MasterPage>
);

Index.getInitialProps = async () => {
    const precio = await fetch('https://min-api.cryptocompare.com/data/pricemultifull?fsyms=BTC&tsyms=USD');
    const jsonPrecio = await precio.json();
    const resPrecio = await jsonPrecio.DISPLAY['BTC']['USD'];

    const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&language=es&apiKey=1926f2bb78fb439896e11f22e100cd7c');
    const jsonNoticias = await noticias.json();

    return {
        precioBitcoin: resPrecio,
        noticiasBitcoin: jsonNoticias
    }
}

export default Index;
